/* About.js */
.about-background {
  min-height: 100vh;
  text-align: center;
  padding: 15vh 0vh;
}

/* AboutContent.js */
.about-title-row {
  padding: 5vh 0vh;
  font-size: 10vh;
}
.about-title {
  font-family: "Bebas Neue", cursive;
  letter-spacing: 0.1em;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: #6d545d;
  animation: titleShadow 10s infinite ease-in;
}
@keyframes titleShadow {
  0%,
  100% {
    text-shadow: none;
  }
  50% {
    text-shadow: 1px 1px 1px #dce2c8, 2px 2px 1px #dce2c8, 1px 1px 1px #dce2c8,
      4px 4px 1px #dce2c8, 5px 5px 1px #dce2c8, 6px 6px 1px #dce2c8,
      7px 7px 1px #dce2c8, 8px 8px 1px #dce2c8, 9px 9px 1px #dce2c8,
      10px 10px 1px #c3cea1;
  }
}
.about-row {
  padding: 0vh 15vh;
  min-height: 78vh;
  align-items: center;
  display: flex;
}
.about-section {
  padding: 15px 0px;
}
.about-hexagon-background {
  padding-top: 15px;
  background: url(../../Assets/Image/hexagon.png);
  background-size: 97% 100%;
  background-repeat: space;
  background-position: bottom;
  text-align: center;
  margin-bottom: 5vh;
}
.about-description-col {
  align-self: center;
}
.about-description {
  font-family: "Open Sans", sans-serif;
  margin-bottom: 0px;
  font-weight: 600;
}
.about-image-col {
  /* background: wheat; */
  /* padding: 20px; */
  /* border-radius: 50%; */
  align-self: end;
}
/* ToolsImage.js */
.about-image-left {
  width: 80%;
  height: 80%;
}
/* LanguageImage.js */
.about-image-right {
  width: 100%;
  height: 100%;
}
@media (max-width: 600px) {
  .about-row {
    padding: 0px 5vh;
  }
}
@media (max-width: 576px) {
  .about-image-left {
    content-visibility: hidden;
    position: absolute;
    left: 0;
  }
}
@media (min-width: 632px) {
  .about-image-left {
    width: 70%;
    height: 70%;
  }
  .about-image-right {
    width: 80%;
    height: 80%;
  }
}
