/* HoneyComb.js */
.about-honeyComb-image {
  -webkit-filter: grayscale(100%);
  opacity: 0.3;
  transition-duration: 0.5s;
}
.about-honeyComb-image:hover {
  -webkit-filter: none;
  opacity: 1;
  display: block;
}
.about-honeyComb-col {
  padding: 0px 1vh !important;
}
.about-honeyComb-row {
  margin-top: -10px !important;
  place-content: center;
}
/* SkillDetails.js */
.skill-details-card {
  height: 43.5vh;
  place-content: center;
  font-family: "Open Sans", sans-serif;
}
/* ImageRight */
#background {
  animation: animate-background 3s ease-in-out infinite;
}
@keyframes animate-background {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(-5%);
  }
  100% {
    transform: translateY(0%);
  }
}
/* ImageLeft */
#actionArrow {
  animation: animate-action 3s infinite linear;
}
@keyframes animate-action {
  0% {
    transform: translate(0%);
  }
  50% {
    transform: translate(2%, -2%);
  }
  100% {
    transform: translate(0%);
  }
}

/* sda */
.article-skills-col {
  padding: 5vh;
}
.article-skills-background {
  /* display: block; */
  /* position: relative; */
  perspective: 1000px;
}
.article-skills-background:before {
  content: "";
  height: 180px;

  width: 180px;
  position: absolute;
  top: calc(8px * -2);
  left: calc(8px * -2);
  border: 2px dashed #d84c6e;
  background-image: repeating-linear-gradient(
    -24deg,
    transparent,
    transparent 4px,
    #d84c6e 0,
    #d84c6e 5px
  );
  z-index: -1;
}
.article-skills-background,
.article-skills-background:before {
  width: 100%;
  height: 100%;
}

.articles__content {
  background-color: #d84c6e;
  color: black;
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  padding: 2vh;
  height: 180px;

  width: 180px;
}
